      <p class="description">
        {{ $store.getters.location.name }}, {{ $store.getters.location.country }}
      </p>
  </div>
</template>

<script>
  export default {
    name: 'menu-bar',
    components: {
  
    },
    data() {
      return {
  
      };
    }
  };
