<style lang="scss">
  .menu-bar {
    width: 100%;
    height: auto;
    position: fixed;
    padding: 1em 1em 1em 1em;
    display: flex;
    flex-direction: row;
      .router{
        color: black;
        margin: 0;
        display: flex;
        justify-content: center;
      }
      .description {
        margin: auto 2em auto 0.5em;
        width: 100%;
        text-align: center;
      }
  }
    .light{
      background: #F5F5F5;
      color: #212121;
      border-bottom: solid 1px #BDBDBD;
      .hamburger{
        background: url("./assets/hamburger-light.svg");
