        height: 20px;
        margin: 0;
        padding: 0;
        border: none;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    .dark{
      background: #000000;
      color: #EEE;
      border-bottom: solid 1px #424242;
      .hamburger{
        background: url("./assets/hamburger-dark.svg");
        width: 20px;
        height: 20px;
        margin: 0;
        padding: 0;
        border: none;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
</style>



